import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service'
import { Router } from '@angular/router';
@Component({
  selector: 'app-events',
  templateUrl: './events.page.html',
  styleUrls: ['./events.page.scss'],
  providers: [HttpService]
})
export class EventsPage implements OnInit {
  currentColor:any;
  response:any;
  data:any;
  constructor(public router: Router,public shared: HttpService) { 
    this.currentColor = 'light';
    
  }

  ngOnInit() {
  }

  ionViewDidEnter() {
    if (navigator.onLine) {
            this.shared.ServiceCallevents.ServiceCallRequestEventsData().then((res) => { 
              // debugger
              
              this.response=res;
              this.data=this.response.content.data;
            
              }, err => {
                
              })
    
          }else{
            this.shared.displayNetworkUpdate('No Internet Connection')
          }
        
      }


      NextPageActivity(data){
        this.router.navigate(['/menu/first/tabs/tab1/events/events-details'], { state: { data: data } });
      }

}
